<template>
  <div class="classify">

    <div class="nav"></div>
    <div class="classify-box">
      <div class="aside">
        <div v-for="(item, index) in asideData" :key="index" @click="changeState(item)">{{item.title}}</div>
      </div>
      <div class="content">
        <h2>分类内容</h2>

        <div>
          <!-- 三级路由 -->
          <router-view></router-view>
        </div>

      </div>
    </div>


  </div>
</template>

<style scoped lang="less">
  .classify>.nav{
    height: 50px;
    background-color: #666;
  }

  .classify-box{
    position: fixed;
    top: 50px;
    bottom: 60px;
    left: 0;
    right: 0;
    background-color: #f5f5f5;
    >div{
      float: left;
      height: 100%;
    }
    >.aside{
      width: 100px;
      overflow-y: auto;
      >div{
        height: 40px;
        line-height: 40px;
        text-align: center;
        border-bottom: 1px solid #ddd;
      }
    }
    >.content{
      width: calc(100% - 100px);
      background-color: #fff;
      overflow-y: auto;
    }
  }
</style>

<script>
  export default {
    name: 'classify',

    data() {
      return {

        asideData: [
          {
            name: 'a',
            title: '图书音像',
            isActive: true
          },
          {
            name: 'b',
            title: '运动户外',
            isActive: false
          }
        ]

      };
    },

    methods: {
      changeState(item) {
        this.$router.push({name: item.name});
      }
    }
  }
</script>>